.checkbox__content {
  position: relative;
  padding-bottom: 10px; }

.back-call__checkbox {
  border: 1px solid #ffffff;
  background-color: $site-color;
  margin: 0 8px;
  position: absolute;
  left: -9999px; }

.back-call__checkbox+label:before {
  content: "";
  position: absolute;
  left: -20px;
  top: 0;
  width: 10px;
  height: 10px;
  border: 1px solid #ffffff;
  background-color: $site-color; }

.back-call__checkbox+label:after {
  content: "";
  position: absolute;
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  transition: all 0.2s ease;
  left: -19px;
  top: 1px;
  width: 8px;
  height: 3px;
  border-radius: 1px;
  border-left: 4px solid #ffffff;
  border-bottom: 4px solid #ffffff;
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg); }

.back-call__checkbox:not(:checked) + label:after, .back-call__checkbox:not(:checked) + label:after {
  opacity: 0; }

.back-call__checkbox:checked + label:after, .back-call__checkbox:checked + label:after {
  opacity: 1; }

